* {
  list-style: none;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

a:link {
  color: black;
}

.header {
  margin: 0 auto;
  margin-top: 10px;
  width: 90%;
  display: flex;
  justify-content: space-between;
}

.homeSection {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.buttonWrapper {
  margin-top: 20px;
  width: 90%;
  display: flex;
  justify-content: space-between;
}

.header button,
.homeSection button {
  width: 70px;
  height: 40px;
}

@media (min-width: 768px) {
  .buttonWrapper {
    margin-top: 50px;
  }
}
